<template>
  <div class="note-sidebar">
    <span class="btn add-note" @click="addNote"></span>

    <el-dropdown class="notebook-title" @command="handleCommand" placement="bottom">
      <span class="el-dropdown-link">
        {{curBook.title}}
        <i class="iconfont icon-down"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
          v-for="notebook in notebooks"
          :command="notebook.id"
          :key="notebook.id"
        >{{notebook.title}}</el-dropdown-item>
        <el-dropdown-item command="trash"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="menu">
      <div></div>
      <div></div>
    </div>
    <ul class="notes">
      <li v-for="note in notes" :key="note.id">
        <router-link :to="`/note?noteId=${note.id}&notebookId=${curBook.id}`">
          <span class="date">{{note.updatedAtFriendly}}</span>
          <span class="title">{{note.title}}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Notebooks from "@/apis/notebook";
import Notes from "@/apis/notes";
import Bus from "@/helpers/bus";

export default {
  created() {
    // notebook
    // notebooks
    Notebooks.getAll()
      .then(res => {
        this.notebooks = res.data;
        //bookcurBook
        // 
        // booklist
        // routequery     
        this.curBook =
          this.notebooks.find(
            notebook => notebook.id == this.$route.query.notebookId
          ) ||
          this.notebooks[0] ||
          {};
        // bookID promis
        return Notes.getAll({ notebookId: this.curBook.id });
      })
      // notes
      // 
      .then(res => {
        this.notes = res.data;
        this.$emit("update:notes", this.notes);
        Bus.$emit("update:notes", this.notes);
      });
  },

  data() {
    return {
      notebooks: [],
      curBook: {},
      notes: []
    };
  },

  methods: {
    handleCommand(notebookId) {
      if (notebookId == "trash") {
        return this.$router.push({ path: "/trash" });
      }
      this.curBook = this.notebooks.find(notebook => notebook.id == notebookId);
      Notes.getAll({ notebookId }).then(res => {
        this.notes = res.data;
        this.$emit("update:notes", this.notes);
      });
    },

    addNote() {
      Notes.addNote({ notebookId: this.curBook.id }).then(res => {
        console.log(res);
        this.notes.unshift(res.data);
      });
    }
  }
};
</script>


<style lang="less" >
@import url(../assets/css/note-sidebar.less);
</style>





