<template>
  <div class="detail" id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate">
        <i class="iconfont icon-plus"></i> 
      </a>
    </header>

    <main>
      <div class="layout">
        <h3>({{ notebooks.length }})</h3>
        <div class="book-list">
          <router-link
            v-for="notebook in notebooks"
            :to="`/note?notebookId=${notebook.id}`"
            class="notebook"
            :key="notebook.id"
          >
            <div>
              <span class="iconfont icon-notebook"></span>
              {{ notebook.title }}
              <span>{{ notebook.noteCounts }}</span>
              <span class="action" @click.stop.prevent="onEdit(notebook)"></span>
              <span class="action" @click.stop.prevent="onDelete(notebook)"></span>
              <span class="date">{{notebook.friendlyCreatedAt}}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
import Auth from "@/apis/auth.js";
import Notebook from "@/apis/notebook";
import { friendlyDate } from "@/helpers/util";

// window.Notebook = Notebook;

export default {
  data() {
    return {
      notebooks: []
    };
  },

  created() {
    Auth.getInfo().then(res => {
      if (!res.isLogin) {
        this.$router.push({ path: "/login" });
      }
    });

    Notebook.getAll().then(res => {
      this.notebooks = res.data;
    });
  },

  methods: {
    onCreate() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancleButtonText: "",
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: "30"
      })
        .then(({ value }) => {
          return Notebook.addNotebook({ title: value });
        })
        .then(res => {
          res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt);
          this.notebooks.unshift(res.data);
          this.$message.success(res.msg);
        });
    },

    onEdit(notebook) {
      let title = "";
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /^.{1,30}/,
        inputValue: noteBook.title,
        inputErrorMessage: "30"
      })
        .then(({ value }) => {
          title = value;
          return Notebook.updateNotebook(notebook.id, { title });
        })
        .then(res => {
          notebook.title = title;
        });
    },

    onDelete(notebook) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          return Notebook.deleteNotebook(notebook.id);
        })
        .then(res => {
          this.notebooks.splice(this.notebooks.indexOf(notebook), 1);
          this.$message.success(res.msg);
        });
    }
  }
};
</script>

<style lang = 'less' scoped>
@import url(../assets/css/notebooklist.less);
</style>


